@import "components/fonts";

body {
    font-family: 'Roboto';
    background: #ffA07A; }
form {
    padding: 15px;
    background: black; }
#wrapper {
    min-height: 700px;
    background: #fa8072; }
.card_block {
    padding: 10px;
    background: rgba(0, 128, 0, 0.507);
    min-height: 200px; }
.card_film {
    min-height: 200px;
    background: white; }
.title {
    min-height: 76px;
    padding: 5px; }
.image {
    margin-bottom: 10px; }
.premiered {
    padding-left: 10px;
    font-size: 12px;
    min-height: 21px; }
.summary {
    height: 200px;
    overflow-y: scroll;
    padding: 10px; }
.genres {
    min-height: 86px; }
.genre_block {
    border: 1px solid black;
    border-radius: 10px;
    padding: 5px;
    margin: 5px;
    font-size: 14px;
    display: inline-block; }
